.container{
  padding: 2rem 1rem;
  display: grid;
  align-items: center;
  justify-content: center;
  position: relative;
  height: calc(100% -2rem);
  background: url('./raster.png') no-repeat  center;
  background-size: cover;
  background-position: center;
  margin: 2rem;
}
.textBlock{
  display:grid;
  gap:2rem;
}
.titleText{
  font-size: 5.2rem;
  font-weight: 600;
}
.textLine{
  display: flex;
  align-items: center;
  flex-flow: row;
  gap: 2rem;
}
.stripe {
  width: 4.8rem;
  display: inline-block;
  vertical-align: middle;
  line-height: normal;
  background-color: black;
  height: .2rem;
  margin-right: 1rem;
}
.subtitleText{
  font-size: 3.6rem;
  font-weight: 400;
}

@media screen and (max-width:500px){
  .container{
    display: none;
  }
}